@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- CSS For Self-Contained TranslationBar --- */

/* Hides the Google Translate banner that appears at the top */
.goog-te-banner-frame.skiptranslate {
    display: none !important;
} 

/* Prevents the page content from being pushed down by the banner */
body { 
    top: 0px !important; 
}

/* Hides the "Powered by" logo and text */
#google_translate_element_container .goog-logo-link,
#google_translate_element_container .goog-te-gadget > span {
    display: none !important;
}

/* Styles the dropdown itself */
#google_translate_element_container select.goog-te-combo {
    background-color: #4B5563; /* gray-600 */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 2px 4px;
    font-size: 0.75rem; /* text-xs */
}
